import React, { useState } from 'react'
import { Card, List, Button } from 'antd';
import { useNavigate } from 'react-router-dom';

const data = [];

const SmartBuyResult = () => {

  const navigate = useNavigate();
  const [Mode, setMode] = useState([])
  const [listName, setlistName] = useState(' ')
  const [price, setprice] = useState(null)
  const firstList = [['GPU', 'i5-13400F'], ['MainBoard', 'B660'], ['GPU', 'RTX 3050'], ['RAM', 'DDR4 8GB * 2']]
  const secondList = [['GPU', 'i3-13100'], ['MainBoard', 'H61'], ['RAM', 'DDR4 8GB']]

  function handleSelectFirst(props) {
    setMode(firstList)
    setlistName(' ')
    setprice(513200)
  }

  function handleSelectSecond() {
    setMode(secondList)
    setlistName(' ')
    setprice(300000)
  }

  function onClick() {
    navigate('/NomalBuy/');
  }
  return (
    <div style={{
      position: 'relative',
      width: '90%',
      height: '500px',
      textAlign: 'center',
      margin: '10px 5%',
    }}>
      <Card title=" " bordered={false}
        style={{
          position: 'relative',
          width: '30%',
          height: '100%',
          display: '',
        }}>
        <Button
          style={{
            margin: '5px auto',
            display: 'block',
          }}
          type="primary"
          onClick={handleSelectFirst}
          key={firstList}>
           
        </Button>
        <Button style={{
          margin: '5px auto',
          display: 'block',
        }}
          onClick={handleSelectSecond}
          value={secondList}>
           
        </Button>
      </Card>
      {/*   */}
      <List
        style={{
          width: '50%',
          position: 'absolute',
          top: '0',
          right: '0'
        }}
        header={<div>{listName}</div>}
        footer={price && <div style={{ textAlign: 'center' }}>{price} </div>}
        bordered
        dataSource={Mode}
        renderItem={(item) => (
          <List.Item>
            <a href='/NomalBuy/'> {item[0]}: {item[1]}</a>
          </List.Item>
        )}
      />
      <Button type="primary" onClick={onClick} style={{ position: 'absolute', right: '30%', top: '70%' }}> </Button>

    </div >
  )
}

export default SmartBuyResult
