import React, { useState } from 'react'
import { Menu } from 'antd';
import Login from '../Login/Login';
import SmartBuy from '../SmartBuy/SmartBuy';

const NavMenu = () => {
  const menu_list = ['', ' ', '', 'HELP CENTER'];
  const items = [
    {
      key: 'menuLogin',
      label: <Login />,
    },
    {
      key: 'menuBuy',
      label: <a href='/SmartBuy/'> </a>,
      children: [{
        key: 'SmartBuy',
        label: <a href='/SmartBuy/'> </a>
      },
      {
        key: 'NomalBuy',
        label: <a href='/NomalBuy/'> </a>
      }]
    },
    {
      key: 'menuMyPage',
      label: menu_list[2],
      children: [{
        type: 'group',
        key: 'menuCart',
        label: ''
      },
      {
        type: 'group',
        key: 'menuOrder',
        label: ''
      }]
    },
    {
      key: 'menuHelpCenter',
      label: menu_list[3],
      children: [{
        type: 'group',
        key: 'menuNotice',
        label: ''
      },
      {
        type: 'group',
        key: 'menuQnA',
        label: '  '
      },
      {
        type: 'group',
        key: 'menuInquiry',
        label: '1:1'
      }]
    },

  ];

  return (
    <div>
      <Menu
        style={{
          position: 'absolute',
          top: '5%',
          right: '0',
          backgroundColor: '#fff',
          border: 'none',
          fontWeight: 'bold',
        }}
        theme="light"
        flex="auto"
        mode="horizontal"
        items={items}
      />
    </div>
  )
}

export default NavMenu
