import React from 'react'
import { Select, Card, Checkbox, Input } from 'antd'

const StepThree = () => {
  const addon = ['', '', '', '', ' ',]
  return (
    <div style={{
      textAlign: 'left',
      marginLeft: '5%',
    }}>
      {addon.map((e, index, arr) => {
        return (
          <React.Fragment key={index}>
            <p>
              <Checkbox>{e}</Checkbox>
            </p>
          </React.Fragment>
        )
      })}
    </div>
  )
}

export default StepThree
